require('@ui5/webcomponents/dist/Dialog');
require('./pages/index.html');
const videoHeroWebm = require("./components/hero/hero.webm");
const videoHeroMp4 = require("./components/hero/hero.mp4");

var url = "https://openui5.org/ui5con/germany2020/?v3";
var oShareConfigurations = {
    facebook: {
        shareUrl: 'https://www.facebook.com/sharer/sharer.php',
        params: {
            u: url
        }
    },
    linkedin: {
        shareUrl: 'https://www.linkedin.com/shareArticle',
        params: {
            url: url,
            mini: true
        }
    },
    twitter: {
        shareUrl: 'https://twitter.com/intent/tweet/',
        params: {
            url: url
        }
    },
    email: {
        shareUrl: 'mailto:',
        params: {
            subject: "UI5con@SAP coming in June 2020",
            body: "Mark your calendars: UI5con@SAP 2020 is on June 19 in St. Leon-Rot, Germany — a daylong community event focused on UI5 development. https://openui5.org/ui5con/germany2020/"
        }
    }
}

window.openShare = function(sShareTarget) {
    var oShareConfiguration = oShareConfigurations[sShareTarget];
    if (!oShareConfiguration) {
        return;
    }

    var oParams = oShareConfiguration.params,
        aQueryParams = Object.keys(oParams).map(function (sParam) {
            return sParam + "=" + encodeURIComponent(oParams[sParam]);
        });

    var sShareUrl = oShareConfiguration.shareUrl + "?" + aQueryParams.join("&") + "&v=2";

    var popWidth = 600,
        popHeight = 480,
        left = window.innerWidth / 2 - popWidth / 2 + window.screenX,
        top = window.innerHeight / 2 - popHeight / 2 + window.screenY,
        popParams = 'scrollbars=no, width=' + popWidth + ', height=' + popHeight + ', top=' + top + ', left=' + left,
        newWindow = window.open(sShareUrl, '', popParams);

    if (window.focus) {
        newWindow.focus();
    }
}

function createVideoElement () {
    var oVideo = document.createElement("video");
    oVideo.muted = true;
    oVideo.loop = true;
    oVideo.playsinline = true;
    oVideo.preload = true;
    oVideo.setAttribute("class", "hero_video");

    var oSource = document.createElement("source");
    oSource.setAttribute("src", videoHeroWebm);
    oSource.setAttribute("type", "video/webm");
    oVideo.appendChild(oSource);

    oSource = document.createElement("source");
    oSource.setAttribute("src", videoHeroMp4);
    oSource.setAttribute("type", "video/mp4");
    oVideo.appendChild(oSource);

    var oHeroElement = document.getElementById("heroBackground");
    oHeroElement.appendChild(oVideo);

    setTimeout(function() {
        oHeroElement.getElementsByClassName("hero_image")[0].style.zIndex = 0;
        oVideo.play();
    }, 1000);
}

var emailShareConfig = oShareConfigurations.email;
var emailShare = document.getElementById("emailShare");
var oParams = emailShareConfig.params;
var aQueryParams = Object.keys(oParams).map(function (sParam) {
    return sParam + "=" + encodeURIComponent(oParams[sParam]);
});
emailShare.href = emailShareConfig.shareUrl + "?" + aQueryParams.join("&");

var hero = document.getElementById("hero");
var heroBackground = document.getElementById("heroBackground");
var bar = document.getElementById("bar");

setTimeout(function () {
    hero.classList.add("animate_step1");
});

setTimeout(function () {
    heroBackground.classList.add("view_focus");
}, 700);

setTimeout(function () {
    heroBackground.classList.add("view_focus");
}, 700);

setTimeout(function () {
    hero.classList.remove("animate_step1");
    hero.classList.add("animate_step2");
    bar.classList.add("view_visible");

    setTimeout(function () {
        hero.classList.remove("animate_step2");
        hero.classList.add("animate_step3");
    }, 500);
}, 1400);

if( !/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
    createVideoElement();
}

var overlay = document.getElementById("overlay");
var dialog = document.getElementById("paperToCallDialog");
var submit = document.getElementById("submit");
var sliderContainer = document.querySelector(".slider > .slider__container");
var sessions = document.querySelector(".sessions");

window.openDialog = function () {
    overlay.style.display = "block";
    dialog.open();
}

window.closeDialog = function () {
    dialog.close();
}

dialog.addEventListener("afterClose", function () {
    overlay.style.display = "none";
    sliderContainer.classList.remove("state_iframe");
    sessions.classList.remove("view_animation");
});

dialog.addEventListener("afterOpen", function () {
    sessions.classList.add("view_animation");
});

submit.addEventListener("click", function () {
    sliderContainer.classList.add("state_iframe");
    sliderContainer.scrollTo(0, 0);
});